export function findKeyByValue(object: any, value: any) {
    return Object.keys(object).find(key => object[key] == value);
};

export function customDateFormat(date: any, formatString: any) {
    var YYYY, YY, MMMM, MMM, MM, M, DDDD, DDD, DD, D, hhhh, hhh, hh, h, mm, m, ss, s, ampm, AMPM, dMod, th;
    YY = ((YYYY = date.getFullYear()) + "").slice(-2);
    MM = (M = date.getMonth() + 1) < 10 ? ('0' + M) : M;
    MMM = (MMMM = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"][M - 1]).substring(0, 3);
    DD = (D = date.getDate()) < 10 ? ('0' + D) : D;
    DDD = (DDDD = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"][date.getDay()]).substring(0, 3);
    th = (D >= 10 && D <= 20) ? 'th' : ((dMod = D % 10) == 1) ? 'st' : (dMod == 2) ? 'nd' : (dMod == 3) ? 'rd' : 'th';
    formatString = formatString.replace("#YYYY#", YYYY).replace("#YY#", YY).replace("#MMMM#", MMMM).replace("#MMM#", MMM).replace("#MM#", MM).replace("#M#", M).replace("#DDDD#", DDDD).replace("#DDD#", DDD).replace("#DD#", DD).replace("#D#", D).replace("#th#", th);
    h = (hhh = date.getHours());
    if (h == 0) h = 24;
    if (h > 12) h -= 12;
    hh = h < 10 ? ('0' + h) : h;
    hhhh = hhh < 10 ? ('0' + hhh) : hhh;
    AMPM = (ampm = hhh < 12 ? 'am' : 'pm').toUpperCase();
    mm = (m = date.getMinutes()) < 10 ? ('0' + m) : m;
    ss = (s = date.getSeconds()) < 10 ? ('0' + s) : s;
    return formatString.replace("#hhhh#", hhhh).replace("#hhh#", hhh).replace("#hh#", hh).replace("#h#", h).replace("#mm#", mm).replace("#m#", m).replace("#ss#", ss).replace("#s#", s).replace("#ampm#", ampm).replace("#AMPM#", AMPM);
}

export function toLocalTimeHHMM(ts: any) {
    let timestamp = new Date(parseInt(ts));
    let hour = timestamp.getHours();
    let minutes = timestamp.getMinutes();
    let localTime = (hour < 10 ? "0" : "") + hour + "h" + (minutes < 10 ? "0" : "") + minutes;
    return localTime;
}

export function toLocalClockTime(ts: any) {
    let timestamp = new Date(parseInt(ts));
    let hour = timestamp.getHours();
    let minutes = timestamp.getMinutes();
    let localTime = hour + "h" + (minutes < 10 ? "0" : "") + minutes;
    return localTime;
}

export function formatUserName(userName: any) {
    let correctUserName = userName.trim();
    if (correctUserName.length > 18) {
        correctUserName = correctUserName.slice(0, 15) + '...';
    }
    return correctUserName;
}

export function secondsToHHMMSS(secs: any) {
    if (secs == 0) return "00:00";
    let sec_num = parseInt(secs, 10);
    let hours = Math.floor(sec_num / 3600);
    let minutes = Math.floor(sec_num / 60) % 60;
    let seconds = sec_num % 60;

    return [hours, minutes, seconds]
        .map(v => v < 10 ? "0" + v : v)
        .filter((v, i) => v !== "00" || i > 0)
        .join(":");
}

export function toLocalTime(ts: any, format: any) {
    return customDateFormat(new Date(ts * 1000), format);
}

export function randRange(min: any, max: any) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

export function toFixed(num, fixed = 0) {
    try {
        Big.RM = 0;
        if (isNaN(+num) || typeof num === "object") num = 0;
        return Big(num).toFixed(fixed);
    } catch (e) { }
    return "0";
}

export function getPostionInOtherNode(spaceNode: any, targetNode: any) {
    if (targetNode.parent == null) {
        return null;
    }
    let pos = new Vec3(0, 0, 0);
    const targetNodeComponent = targetNode.parent.getComponent(UITransform);
    const spaceNodeComponent = spaceNode.getComponent(UITransform);
    if (!targetNodeComponent) {
        targetNode.parent.addComponent(UITransform);
    }
    if (!spaceNodeComponent) {
        spaceNode.addComponent(UITransform);
    }
    pos = targetNodeComponent.convertToWorldSpaceAR(targetNode.getPosition());
    return spaceNodeComponent ? spaceNodeComponent.convertToNodeSpaceAR(pos) : pos;
};

function getSpriteNameFromAtlas(uuid) {
    const mainUUID = uuid.split('@')[0];
    const mainAsset = assetManager.assets['_map'][mainUUID];
    if (mainAsset) {
        const keys = Object.keys(mainAsset.spriteFrames)
        for (let i = 0; i < keys.length; i++) {
            if (mainAsset.spriteFrames[keys[i]]._uuid == uuid) {
                return keys[i];
            }
        }
    }
    return '';
}

function isSpriteFrameFromAtlas(uuid) {
    const mainUUID = uuid.split('@')[0];
    const mainAsset = assetManager.assets['_map'][mainUUID];
    if (mainAsset && mainAsset instanceof SpriteAtlas) {
        return true;
    }
    return false;
}

export function convertAssetArrayToObject(arr: any) {
    let obj: any = {};
    for (let i = 0; i < arr.length; i++) {
        if (arr[i]._name == "" && arr[i].data) {
            arr[i]._name = arr[i].data._name;
        }
        if (arr[i]) {
            let uuid = arr[i]._uuid ? arr[i]._uuid : arr[i].uuid;
            const name = (uuid && isSpriteFrameFromAtlas(uuid)) ? getSpriteNameFromAtlas(uuid) : arr[i]._name;
            obj[name] = arr[i];
        }
    }
    return obj;
};

export function getAnimationsName(spine: any) {
    return Object.keys(spine.skeletonData._skeletonJson.animations);
}

/**
 * ! just using for the spine have a few animations
 */
export function setMixAllAnims(spine: any, mixTime = 0.15) {
    const animNames = getAnimationsName(spine);
    if (animNames.length < 2) {
        error("spine have to have many animations");
        return;
    }
    for (let i = 0; i < animNames.length; i++) {
        for (let j = 0; j < animNames.length; j++) {
            spine.setMix(animNames[i], animNames[j], mixTime);
        }
    }
}

export function convertPayLineAllways(payLines: any = [], multiplier = 1, betDenom: any) {
    const listNewPL = [];
    for (let i = 0; i < payLines.length; i++) {
        if (payLines[i].includes(';')) {
            const dataSplit = payLines[i].split(';');
            if (dataSplit.length !== 0) {
                listNewPL.push({
                    symbolId: dataSplit[0],
                    totalWinAmount: dataSplit[1],
                    symbolCount: dataSplit[2],
                    combination: dataSplit[3],
                    payableSymbol: dataSplit[4],
                    betDenom,
                    multiplier,
                });
            }
            else {
                error("payLines is not right AKTV formatted");
            }
        }
    }
    return listNewPL;
};

export function convertPayLine(payLines: string[]) {
    const listNewPL = [];
    for (let i = 0; i < payLines.length; i++) {
        const dataSplit = payLines[i].split(';');
        if (dataSplit.length >= 3) {
            listNewPL.push({
                payLineID: dataSplit[0],
                payLineWinNumbers: parseInt(dataSplit[1]),
                payLineWinAmount: dataSplit[2],
                payLineSymbol: dataSplit[3]
            });
        }
    }
    return listNewPL;
};

export function formatWalletMoney(num, digits = 2) {
    if (UTIL_CONFIG.CURRENCY_CONFIG) {
        return formatWalletMoneyByCurrency(num, digits);
    }
    return formatWallet(num, digits);
}
export function formatMoney(amount, decimalCount?, decimal = ".", thousands = ",") {
    if (UTIL_CONFIG.CURRENCY_CONFIG) {
        if (isNaN(decimalCount) || decimalCount === null) decimalCount = UTIL_CONFIG.CURRENCY_CONFIG.DECIMAL_COUNT;
        return formatMoneyByCurrency(amount, decimalCount, decimal, thousands);
    }
    return formatCoin(amount, decimalCount || 0, decimal, thousands);
}

function formatMoneyByCurrency(amount, decimalCount = 0, decimal = ".", thousands = ",") {
    const { CENT_UNIT, CENT_SUFFIX, CURRENCY_PREFIX, DECIMAL_COUNT } = UTIL_CONFIG.CURRENCY_CONFIG;
    let prefix = CURRENCY_PREFIX || "";
    let suffix = "";
    if (isNaN(decimalCount) || decimalCount === null) {
        decimalCount = decimalCount || DECIMAL_COUNT;
    }
    if (isNaN(amount) || amount === null) return "";

    if (CENT_UNIT && Big(amount).lt(CENT_UNIT)) {
        prefix = "";
        suffix = CENT_SUFFIX || "";
        amount = Big(amount || 0).times(100 || 0).toNumber();
    }
    return prefix + formatCoin(amount, decimalCount || 0, decimal, thousands) + suffix;
}

function formatWalletMoneyByCurrency(num, digits) {
    const { CENT_UNIT, CENT_SUFFIX, CURRENCY_PREFIX, DECIMAL_COUNT, TRIM_ZERO } = UTIL_CONFIG.CURRENCY_CONFIG;
    let prefix = CURRENCY_PREFIX || "";
    let suffix = "";
    if (isNaN(digits) || digits === null) {
        digits = DECIMAL_COUNT || 2;
    }
    if (isNaN(num) || num === null) return "";
    
    let trimZero = TRIM_ZERO;
    if (CENT_UNIT && Big(num).lt(CENT_UNIT)) {
        prefix = "";
        suffix = CENT_SUFFIX || "";
        num = Big(num || 0).times(100 || 0).toNumber();
    }
    return prefix + formatWallet(num, digits, trimZero) + suffix;
}

function formatWallet(num, digits, trimZero = true) {
    if (isNaN(parseFloat(num))) num = 0;
    const si = [
        { value: 1, symbol: "" },
        { value: 1E3, symbol: "K" },
        { value: 1E6, symbol: "M" },
        { value: 1E9, symbol: "B" },
        { value: 1E12, symbol: "T" },
        { value: 1E15, symbol: "P" },
        { value: 1E18, symbol: "E" }
    ];
    const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
    let i;
    for (i = si.length - 1; i > 0; i--) {
        if (num >= si[i].value) break;
    }
    let wallet = toFixed((num / si[i].value), digits);
    if (trimZero) wallet = wallet.replace(rx, "$1");
    wallet = wallet + si[i].symbol;
    if (UTIL_CONFIG.FORMAT_MONEY_EVENT) {
        wallet = wallet.replace('.',',');
    }
    return wallet;
}

export function changeParent(node: any, newParent: any) {
    if (node.parent === newParent) return;
    const getWorldRotation = function (node: any) {
        let currNode = node;
        let resultRot = currNode.angle || 0;
        do {
            currNode = currNode.parent;
            resultRot += currNode.angle || 0;
        } while (currNode.parent != null);
        resultRot = resultRot % 360;
        return resultRot;
    };

    const oldWorRot = getWorldRotation(node);
    const newParentWorRot = getWorldRotation(newParent);
    const newLocRot = oldWorRot - newParentWorRot;

    if (!node.getComponent(UITransform)) {
        node.addComponent(UITransform);
    }
    if (!newParent.getComponent(UITransform)) {
        newParent.addComponent(UITransform);
    }
    const oldWorPos = node.getComponent(UITransform).convertToWorldSpaceAR(new Vec3(0, 0, 0));
    const newLocPos = newParent.getComponent(UITransform).convertToNodeSpaceAR(oldWorPos);

    node.parent = newParent;
    node.position = newLocPos;
    node.angle = newLocRot;
};

export function getRandomInt(min: any, max: any) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
};

export function roundDecimal(num: number, numDecimal: any) {
    let rate = 1;
    for (let i: number = 0; i < numDecimal; i++) {
        rate *= 10;
    }
    return Math.round((num + Number.EPSILON) * rate) / rate;
};

export function shuffleArray(array: any[]) {
    for (var i = array.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
    return array;
}

export function getRandomElement(array: any[]) {
    if (!array || array.length <= 0) throw new Error("invalid array");
    return array[Math.floor(Math.random() * array.length)];
}

export function pickOutRandomElement(array: any[]) {
    if (!array || array.length <= 0) throw new Error("invalid array");
    let randomIndex = Math.floor(Math.random() * array.length);
    return array.splice(randomIndex, 1)[0];
}

export function removeElementByValue(array: any[], value: any) {
    array.splice(array.indexOf(value), 1);
    return array;
}
export function replaceElement(array: any[], oldElement: any, newElement: any) {
    let index = array.indexOf(oldElement);
    if (index > -1) array[index] = newElement;
    return array;
}

export function getText(text = '', length = 0) {
    let result = text;
    const n = length - text.length;
    if (n) {
        for (var i = 0; i < n; i++) {
            result += ' ';
        }
    }
    return result;
};

export function getLeftTime(endTime: any, serverTime: any, millisecond = 1000) {
    serverTime = serverTime ? new Date(serverTime).getTime() : new Date().getTime();
    let time = Math.floor((new Date(endTime).getTime() - serverTime) / millisecond);
    if (time < 0) time = 0;
    return time;
};

export function addZero(i: any) {
    if (i < 10) {
        i = '0' + i;
    }
    return i;
}

export function getTime() {
    const d = new Date();
    const h = addZero(d.getHours());
    const m = addZero(d.getMinutes());
    const s = addZero(d.getSeconds());
    const t = h + ':' + m + ':' + s;
    return t;
};

export function mapObjectKey(obj: any, keysMap: any) {
    return lodash.transform(obj, function (result: any, value: any, key: any) {
        var currentKey = key;
        if (keysMap[key]) {
            currentKey = keysMap[key].name;
            if (keysMap[key].type == Boolean)
                value = (value == 'T');
        }
        if (lodash.isObject(value)) {
            result[currentKey] = mapObjectKey(value, keysMap);
        }
        else {
            result[currentKey] = value;
        }
    });
}

export function fadeTo(target: Node, duration: number, opacity: number, options?: ITweenOption): Tween<object> {

    let render2D = target.getComponent(UIRenderer);
    options = options || {};

    if (target['tweenFade']) {
        target['tweenFade'].stop();
        target['tweenFade'] = null;
    }
    let tweenFade = null;
    if (render2D) {
        let obj = { alpha: render2D.color.a }
        options.onUpdate = function () {
            if (render2D && render2D.color) {
                setRenderColor(render2D, obj.alpha);
            }
        }
        tweenFade = tween(obj)
            .to(duration, { alpha: opacity }, options)
            .call(() => {
                if (target) {
                    target['tweenFade'] = null;
                }
            })
            .start();
        target['tweenFade'] = tweenFade;
    }
    else {
        let uiOpacity = target.getComponent(UIOpacity);
        if (uiOpacity === null) {
            uiOpacity = target.addComponent(UIOpacity);
        }
        tweenFade = tween(uiOpacity);
        target['tweenFade'] = tweenFade;
        tweenFade
            .to(duration, { opacity }, options)
            .call(() => {
                target['tweenFade'] = null;
            }).start();
    }
    return tweenFade;
}

export function fadeIn(target: Node, duration: number, options?: ITweenOption): Tween<Node> {
    return fadeTo(target, duration, 255, options);
}

export function fadeOut(target: Node, duration: number, options?: ITweenOption): Tween<Node> {
    return fadeTo(target, duration, 0, options);
}
export function getOpacity(node: Node) {
    let uiOpacity: UIOpacity = node.getComponent(UIOpacity);
    if (uiOpacity === null) uiOpacity = node.addComponent(UIOpacity);
    return uiOpacity.opacity;
}

export function setOpacity(node: Node, opacity: number) {
    let render2D = node.getComponent(UIRenderer);
    let uiOpacity: UIOpacity = node.getComponent(UIOpacity);

    if (render2D) {
        if (uiOpacity) {
            uiOpacity.opacity = 255;
        }
        setRenderColor(render2D, opacity);
    }
    else {
        if (uiOpacity === null) {
            uiOpacity = node.addComponent(UIOpacity);
        }
        uiOpacity.opacity = opacity;
    }
    if (node) { //cheat for v373 to flag dirty transform
        node.position = node.position;
    }
}

export function setOpacityDeep(node: Node, opacity: number) {
    setOpacity(node, opacity);
    let childRenders = node.getComponentsInChildren(UIRenderer);
    childRenders.forEach(it => {
        setOpacity(it.node, opacity);
    })
}

export function fadeOutDeep(target: Node, duration: number, options?: ITweenOption): Tween<Node> {
    return fadeToAllChild(target, duration, 0, options);
}

export function fadeInDeep(target: Node, duration: number, options?: ITweenOption): Tween<Node> {
    return fadeToAllChild(target, duration, 255, options);
}

//fade all renderer in node child, use it carefully
export function fadeToAllChild(target: Node, duration: number, opacity: number, options?: ITweenOption): Tween<object> {

    let render2D = target.getComponent(UIRenderer);
    let childRenders = target.getComponentsInChildren(UIRenderer);
    options = options || {};

    if (target['tweenFade']) {
        target['tweenFade'].stop();
        target['tweenFade'] = null;
    }
    let tweenFade = null;
    if (render2D) {
        let obj = { alpha: render2D.color.a }
        options.onUpdate = function () {
            childRenders.forEach(it => {
                setRenderColor(it, obj.alpha);
            })
        }
        tweenFade = tween(obj)
        .to(duration, {alpha: opacity}, options)
        .call(()=>{
            if (target) {
                target['tweenFade'] = null;
            }
        })
        .start();
        target['tweenFade'] = tweenFade;
    }
    else {
        let uiOpacity = target.getComponent(UIOpacity);
        if (uiOpacity === null) {
            uiOpacity = target.addComponent(UIOpacity);
        }
        options.onUpdate = function () {
            childRenders.forEach(it => {
                setRenderColor(it, uiOpacity.opacity);
            })
        }
        tweenFade = tween(uiOpacity);
        target['tweenFade'] = tweenFade;
        tweenFade
            .to(duration, { opacity }, options)
            .call(() => {
                target['tweenFade'] = null;
            }).start();
    }
    return tweenFade;
}

export function setRenderColor(render: UIRenderer, alpha: number) {
    let color: Color = Color.clone(render.color);
    color.a = alpha;
    render.color = color;
}

export function tweenMoney(label: Label, duration: number, endValue: number, options?: ITweenOption): Tween<any> {
    if (UTIL_CONFIG.CURRENCY_CONFIG) {
        return tweenMoneyByCurrency(label, duration, endValue, options);
    }

    if (label["_tweenMoney"]) label["_tweenMoney"].stop(); // stop if on tween;
    let currentVal: number = parseInt(label.string.replace(/[,.]/g, "")) || 0;
    const _target = { value: currentVal };
    options = options || {};
    let count = 0;
    const gap = options["gap"] || 3;
    delete options["gap"];
    options.onUpdate = function (target, ratio) { 
        count++;
        if (count >= gap || ratio === 1) {
            count = 0;
            label.string = formatMoney(_target.value,  null, '.', ','); 
        }
    }
    let tweenMoney = tween(_target)
        .to(duration, { value: endValue }, options)
        .call(() => {
            label.string = formatMoney(endValue,  null, '.', ',');
            label["_tweenMoney"] = null;
        })
        .start();
    label.string = formatMoney(currentVal,  null, '.', ',');
    label["_tweenMoney"] = tweenMoney;
    return tweenMoney;
}

export function tweenMoneyByCurrency(label: Label, duration: number, endValue: number, options?: ITweenOption): Tween<any> {
    if (label["_tweenMoney"]) label["_tweenMoney"].stop(); // stop if on tween;
    const { CENT_SUFFIX } = UTIL_CONFIG.CURRENCY_CONFIG;
    const isCentNumber= label.string.includes(CENT_SUFFIX);
    let currentVal = Number(label.string.replace(/[^\d.]/g, ''));
    if (isCentNumber) {
        currentVal = Big(currentVal || 0).div(100 || 0).toNumber();
    }
    const _target = { value: currentVal };
    options = options || {};
    options.onUpdate = function () { label.string = formatMoney(Number(_target.value),  null, '.', ','); }
    let tweenMoney = tween(_target)
        .to(duration, { value: Number(endValue) }, options)
        .call(() => {
            label.string = formatMoney(Number(endValue),  null, '.', ',');
            label["_tweenMoney"] = null;
        })
        .start();
    label["_tweenMoney"] = tweenMoney;
    return tweenMoney;
}

//for slot event only
export function tweenMoney2(label: Label, duration: number, endValue: number, options?: ITweenOption): Tween<any> {
    if (label["_tweenMoney"]) label["_tweenMoney"].stop(); // stop if on tween;

    let currentVal: number = parseInt(label.string.replace(/[,.]/g, "")) || 0;
    const _target = { value: currentVal };
    options = options || {};
    options.onUpdate = function () { label.string = formatMoney(_target.value, 0, ',', '.'); }
    let tweenMoney = tween(_target)
        .to(duration, { value: endValue }, options)
        .call(() => {
            label.string = formatMoney(endValue, 0, ',', '.');
            label["_tweenMoney"] = null;
        })
        .start();
    label["_tweenMoney"] = tweenMoney;
    return tweenMoney;
}


export function tweenWalletMoney(label: Label, duration: number, startValue: number, endValue: number, options?: ITweenOption): Tween<any> {
    if (label["tweenWalletMoney"]) label["tweenWalletMoney"].stop(); // stop if on tween;

    let currentVal: number = startValue || 0;
    const _target = { value: currentVal };
    options = options || {};
    options.onUpdate = function () { label.string = formatWalletMoney(_target.value); }
    const tweenWalletMoney = tween(_target)
        .to(duration, { value: endValue }, options)
        .call(() => {
            label.string = formatWalletMoney(endValue);
            label["tweenWalletMoney"] = null;
        })
        .start();
    label["tweenWalletMoney"] = tweenWalletMoney;
    return tweenWalletMoney;
}

export function tweenBezier2DTo(target: Node, duration: number, c1: Vec2, c2: Vec2, to: Vec2, opts?: ITweenOption) {
    if (target['bezierX']) target['bezierX'].stop();
    if (target['bezierY']) target['bezierY'].stop();

    let c0x = c1.x, c0y = c1.y,
        c1x = c2.x, c1y = c2.y;
    const _targetX = { value: target.getPosition().x };
    const _targetY = { value: target.getPosition().y };
    const aOpts: ITweenOption = opts ? {...opts} : Object.create(null);
    aOpts.progress = function (startX: number, endX: number, currentX: number, t: number) {
        currentX = bezier(startX, c0x, c1x, endX, t);
        return currentX;
    }
    aOpts.onUpdate = function () {
        if (target && target.position) {
            target.setPosition(new Vec3(_targetX.value, target.position.y, target.position.z));
        }
    }
    const bOpts: ITweenOption = opts ? {...opts} : Object.create(null);
    bOpts.progress = function (startY: number, endY: number, currentY: number, t: number) {
        currentY = bezier(startY, c0y, c1y, endY, t);
        return currentY;
    }
    bOpts.onUpdate = function () {
        if (target && target.position) {
            target.setPosition(new Vec3(target.position.x, _targetY.value, target.position.z));
        }
    }
    target['bezierX'] = tween(_targetX)
        .to(duration, { value: to.x }, aOpts)
        .call(() => {
            target['bezierX'] = null;
        })
        .start();
    target['bezierY'] = tween(_targetY)
        .to(duration, { value: to.y }, bOpts)
        .call(() => {
            target['bezierY'] = null;
        })
        .start();
}

export function startGameFromEvent(gameId) {
    director.loadScene("MultiSlot", () => {
        const multiSlotComp = director.getScene().getChildByName('Canvas').getChildByName('MultiSlot');
        if (multiSlotComp) {
            let config = {
                gameId,
                startScene: `g${gameId}`
            };

            (multiSlotComp as any).emit('RQ_PLAY_FROM_EVENT', { config });
        }
    });
}

export function removeDuplicates(array) {
    return array.filter((item: any, index: number) => array.indexOf(item) === index)
};

/**
 * @returns degree angle
*/
export function getAngleTwoPoint(p1: Vec2, p2: Vec2): number {
    return Math.atan2(p2.y - p1.y, p2.x - p1.x) * 180 / Math.PI;
};

export function getDistance(p1: Vec2, p2: Vec2): number {
    return Math.sqrt((p1.x - p2.x) ** 2 + (p1.y - p2.y) ** 2);
};

export function shaking(node: Node, { duration = 0.16, distance = 10, repeat = 1 }) {
    const dur = duration / 8;
    const shake = tween()
        .by(dur, { position: v3(0, distance) })
        .by(dur, { position: v3(0, -distance) })
        .by(dur, { position: v3(0, -distance) })
        .by(dur, { position: v3(0, distance) })
        .by(dur, { position: v3(distance, 0) })
        .by(dur, { position: v3(-distance, 0) })
        .by(dur, { position: v3(-distance, 0) })
        .by(dur, { position: v3(distance, 0) })

    const tweenShake = tween(node).repeat(repeat, shake).start();
    return tweenShake;
}


